import React from 'react'
import './services.css'
import {BiCheck} from 'react-icons/bi'
const Services = () => {
  return (
    <section id='services'>
      <h5>What I Offer</h5>
      <h2>Services</h2>
      <div className='container services__container'>
        <article className='service'>
          <div className='service__head'>
            <h3>UI/UX Design</h3>
          </div>
          <ul className='service__list'>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Skills:Wireframing and Prototyping,
                Photoshop, Illustrator and
                Figma.</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Create wireframes and prototypes using industry-standard design tools.</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Conduct user research and analyze feedback to identify user pain points.</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Contribute to user interface pattern libraries and design documentation.</p>
            </li>

          </ul>
        </article>

        <article className='service'>
          <div className='service__head'>
            <h3>Web Development</h3>
          </div>
          <ul className='service__list'>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Skills:

Front-End:

HTML5,
CSS3,
JavaScript,
React,
Bootstrap
Back-End:
Node.js,
Python,
MySQL,
MongoDB</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Develop responsive websites and web applications using HTML5, CSS3, JavaScript, and React.</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p> Proficient in front-end and back-end technologies using Node.js, Python for backend and database MySQL, MongoDB.</p>
            </li>
  


          </ul>
        </article>

        <article className='service'>
          <div className='service__head'>
            <h3>Data Analysis</h3>
          </div>
          <ul className='service__list'>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Skills:

Data analytics,
Python,
SQL,
Excel (Advanced),
Tableau, Power BI, Matplotlib, Pandas, NumPy,
Database Management







</p>
            </li>
  
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Collect, clean, and preprocess data from various sources to ensure accuracy and consistency.</p>
            </li>
            <li>
              <BiCheck className='service__list-icon'/>
              <p>Proficient in data cleaning, data visualization, statistical analysis, and data modeling.</p>
            </li>

          </ul>
        </article>

      </div>
    </section>
  )
}

export default Services
