import React from 'react'
import './about.css'
import ME from '../../assets/about.jpg'
import {FaAward} from 'react-icons/fa'
import {FiUsers} from 'react-icons/fi'
import {VscFolderLibrary} from 'react-icons/vsc'

const About = () => {
  return (
    <section id='about'>
      <h5>Get to Know</h5>
      <h2>About Me</h2>
      <div className='container about__container'>
        <div className='about__me'>
          <div className='about__me-image'>
            <img src={ME} alt=" About Image" />
          </div>
        </div>
        <div className='about__content'>
          <div className="about__cards">
            <article className='about__card'>
              <FaAward className='about__icon'/>
              <h5>Education</h5>
              <small>10th-96.4%</small><hr></hr>
              <small>12th-95%</small><hr></hr>
              <small>BTech-9.14CGPA</small>
            </article>
            <article className='about__card'>
              <FiUsers className='about__icon'/>
              <h5>Internships</h5>
              <small>3 Tech and Non-Tech</small>
            </article>
            <article className='about__card'>
              <VscFolderLibrary className='about__icon'/>
              <h5>Projects</h5>
              <small>Many Completed Projects at college</small>
            </article>
          </div>
          <p>
          Motivated, cooperative, and responsible Web Developer and Data Analyst with a strong commitment to make positive impact. Possesses strong technical skills rooted in substantial training as an engineer.
           Maintains a highly disciplined work ethic, consistently delivering high-quality results.
          </p>
          <a href='#contact' className='btn btn-primary'>Let's Talk</a>

        </div>
      </div>
      
    </section>
  )
}

export default About
