import React from 'react'
import './portfolio.css'
import IMG1 from '../../assets/qs1.jpg'
import IMG2 from '../../assets/PR2.jpg'
import IMG3 from '../../assets/PR3.jpg'
import IMG4 from '../../assets/PR44.png'
import IMG5 from '../../assets/PR5.jpg'
import IMG6 from '../../assets/PR6.png'
import PR1 from '../../assets/PR1.pdf'
import PR2 from '../../assets/PR2.pdf'
import PR3 from '../../assets/PR3.pdf'
import PR4 from '../../assets/PR4.pbix'
import PR5 from '../../assets/PR5.pdf'
import PR6 from '../../assets/PR6.pptx'

const data=[{
  id: 1,
  image: IMG1,
  title: 'Queen PDF Scanner App',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR1
},
{ 
  id: 3,
  image: IMG3,
  title: 'IOT based Smart Dustbin',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR3
},
{ 
  id: 2,
  image: IMG2,
  title: 'AI ChatGpt3 Clone',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR2
},

{
  id: 4,
  image: IMG4,
  title: 'Data Analysis Project',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR4

},
{
  id: 5,
  image: IMG5,
  title: 'Machine Learning Disease Prediction System',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR5
},
{ 
  id: 6,
  image: IMG6,
  title: 'Journey of Stars',
  github: 'https://github.com/unnati-agarwal-123',
  demo: PR6
}
]
const Portfolio = () => {
  return (
    <section id='portfolio'>
      <h5>My Recent Work</h5>
      <h2>Portfolio</h2>
      <div className='container portfolio__container'>
        {
          data.map(({id,image,title,github,demo})=>{
            return(
            <article key={id} className='portfolio__item'>
            <div className='portfolio__item-image'>
              <img src={image} alt={title} />
            </div>
            <h3>{title}</h3>
            <div className="portfolio__item-cta">
              <a href={github} className='btn' target='_blank'>Github</a>
              <a href={demo} download className='btn btn-primary' target='_blank'>Project Report</a>
            </div>
          </article>
            )
          })
          }
      </div>

    </section>
  )
}

export default Portfolio
