import React from 'react'
import './Experience.css'
import {BsPatchCheckFill} from 'react-icons/bs'

const Experience = () => {
  return (
    <section id='experience'>
      <h5>What Skills I have</h5>
      <h2>My Experience</h2>
      <div className='container experience__container'>
        <div className='experience__frontend'>
          <h3>Full Stack Web Developer</h3>
          <div className='experience__content'>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div>
              <h4>HTML/CSS</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div>
                <h4>Javascript</h4>
                <small className='text-align'>Intermediate</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div>
              <h4>React</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div>
                <h4>MongoDB</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>My SQL</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div>
              <h4>Python</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
          </div>
        </div>


        <div className='experience__backend'>
        <h3>Data Analyst</h3>
          <div className='experience__content'>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>Microsoft Office</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>Tableau</h4>
              <small className='text-align'>Intermediate</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>Power BI</h4>
              <small className='text-align'>Experienced</small></div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>Python</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
            <article className='experience__details'>
              <BsPatchCheckFill className='experience__deatils-icon'/>
              <div><h4>SQl</h4>
              <small className='text-align'>Experienced</small>
              </div>
            </article>
          </div>
        </div>
        </div>
    </section>
  )
}

export default Experience
