import React from 'react'
import './contact.css'
import {MdOutlineEmail} from 'react-icons/md'
import {RiMessengerLine} from 'react-icons/ri'
import {BsWhatsapp} from 'react-icons/bs'
import { useRef } from 'react';
import emailjs from 'emailjs-com'

const contact = () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_pzuoqrn', 'template_1s2m8sv', form.current, 'rMf5BLuU8nfb2dtbBz')
      e.target.reset()
  };

  return (
    <section id='contact'>
      <h5>Get In Touch</h5>
      <h2>Contact Me</h2>
      <div className='container contact__container'>
        <div className="contact__options">
          <article className='contact__option'>
            <MdOutlineEmail className='contact__option-icon'/>
            <h4>Email</h4>
            <h5>unnati.agarwal.2704@gmail.com</h5>
            <a href='mailto:unnati.agarwal.2704@gmail.com' target='_blank'>Send a message</a>
          </article>
          <article className='contact__option'>
            <RiMessengerLine className='contact__option-icon'/>
            <h4>Instagram</h4>
            <h5>unnatiiii_agarwal</h5>
            <a href='www.instagram.com' target='_blank'>Send a message</a>
          </article>
          <article className='contact__option'>
          <BsWhatsapp className='contact__option-icon'/>
            <h4>+918604480912</h4>
            <h5>unnati.agarwal.2704@gmail.com</h5>
            <a href='https://api.whatsapp.com/send?phone=+918604480912' target='_blank'>Send a message</a>
          </article>
        </div>
        <form ref={form} onSubmit={sendEmail}>
          <input type='text' name='name' placeholder='Your Full Name' required/>
          <input type='email' name='email' placeholder='Your Email' required/>
          <textarea name='message' rows='7' placeholder='Your Message' required/>
          <button type='submit' className='btn btn-primary'>Send Message</button>
        </form>
      </div>
    </section>
  )
}

export default contact
